import React, { useState, useEffect } from 'react';

import AppHeader from './AppHeader';
import AppFooter from './AppFooter';
import Box from '@mui/joy/Box';
import Container from '@mui/joy/Container';

import siteMetadata from '../data/siteMetadata';

export interface AppContainerProps {
  title?: string;
  isArticle?: boolean;
  children?: any;
}

const AppContainer: React.FC<AppContainerProps> = function (props) {
  const { children } = props;
  const title = props.title || '';
  const isArticle = props.isArticle || false;

  useEffect(() => {
    document.title = title ? `${title} - ${siteMetadata.title}` : siteMetadata.title;
  });

  return (
    <Box>
      <AppHeader title={title} isArticle={isArticle}></AppHeader>
      <Container
        sx={{
          position: 'relative',
        }}
      >
        {children}
      </Container>
      <AppFooter></AppFooter>
    </Box>
  );
};

export default AppContainer;
