import React, { useState } from 'react';

import Container from '@mui/joy/Container';
import Box, { BoxProps } from '@mui/joy/Box';
import Tooltip from '@mui/joy/Tooltip';
import IconButton from '@mui/joy/IconButton';
import GitHubIcon from '@mui/icons-material/GitHub';

interface AppHeaderProps extends BoxProps {
  title: string;
  isArticle: boolean;
}

const HEIGHT = 54;
const FONT_SIZE = '1.105rem';

const GITHUB_REPO = 'https://github.com/memset0/blog';

const headerStyles = {
  p: 2,
  gap: 2,

  display: 'flex',
  flexDirection: 'row',
  gridColumn: '1 / -1',

  alignItems: 'center',
  justifyContent: 'space-between',

  position: 'sticky',
  top: 0,
  zIndex: 40214,

  backgroundColor: 'rgba(255,255,255,0.8)',
  backdropFilter: 'blur(8px)',
};

export default (function Header(props) {
  return (
    <Box
      component="header"
      className="Header"
      {...props}
      sx={[
        headerStyles,
        {
          height: HEIGHT,
          maxHeight: HEIGHT,
        },
        {
          borderBottom: '1px solid',
          borderColor: 'divider',
        },
        ...(Array.isArray(props.sx) ? props.sx : [props.sx]),
      ]}
    >
      <Container
        sx={{
          position: 'relative',
        }}
      >
        <Box
          sx={{
            fontSize: FONT_SIZE,
            width: '100%',
            height: HEIGHT,
            lineHeight: HEIGHT + 'px',
            textAlign: 'center',
          }}
        >
          {props.isArticle ? props.title : ''}
        </Box>
        <Tooltip
          title={'Github Repo'}
          enterDelay={300}
          sx={{
            position: 'absolute',
            top: (HEIGHT - 40) / 2,
            right: 24,
          }}
        >
          <IconButton component="a" color="primary" href={GITHUB_REPO} target="_blank" data-ga-event-category="header" data-ga-event-action="github">
            <GitHubIcon fontSize="small" />
          </IconButton>
        </Tooltip>
      </Container>
    </Box>
  );
} as React.FC<AppHeaderProps>);
